.menu-item {
	min-width: 30vw;
	height: 27vh;
	flex: 1 1 auto;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 1px solid black;
	margin: 0 7.5px 15px;
	overflow: hidden;

	&:hover {
		cursor: pointer;
		& .background-image {
			transform: scale(1.1);
			transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
		}

		& .content {
			opacity: 0.9;
		}
	}

	&.large {
		height: 380px;
	}
	&:first-child {
		margin-right: 7.5px;
	}

	&:last-child {
		margin-left: 7.5px;
	}
	.background-image {
		width: 100%;
		height: 100%;
		background-position: center;
		background-size: cover;
	}
	.content {
		height: calc(15px + 7vh);
		padding: 0 25px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		border: 1px solid black;
		background-color: white;
		opacity: 0.7;
		position: absolute;

		.title {
			font-weight: bold;
			margin-bottom: 6px;
			font-size: calc(3px + 1vw);
			color: #4a4a4a;
		}

		.subtitle {
			font-weight: lighter;
			font-size: calc(3px + 0.8vw);
		}
	}
}
